import STATE_core from '@/store/STATE_core.js';
import Functions from '@/logic/Functions.js';
import Request_service from '@/logic/Request_service.js';

/**
 * Forms_extended
*/
class Forms_extended
{
	/**
	 * validate_extended_form
	*/
	/************************************************************************************/
	/* currently not in use, sending data in calculate instead of validate */
	/************************************************************************************/
	validate_extended_form()
	{
		let requestParams = {
			action_name: 'validategroupform',
			method: 'POST',
			body: Functions.serialize_user_input(),
			url: STATE_core().endpoint_url() + 'online/validategroupform/' + STATE_core().product_group_id() + '/?global=0&without_form=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}

	/**
	 * get_tertiary_form
	*/
	get_tertiary_form()
	{
		let requestParams = {
			action_name: 'getform',
			method: 'POST',
			body: null,
			url: STATE_core().endpoint_url() + 'online/getform/' + 146 + '/?json=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}

	/**
	 * validate_tertiary_form
	*/
	validate_tertiary_form()
	{
		let requestParams = {
			action_name: 'validateform',
			method: 'POST',
			body: Functions.serialize_user_input(),
			url: STATE_core().endpoint_url() + 'online/validateform/' + 146 + '/?without_form=1',
		};
		requestParams.url += Functions.add_token_as_parameter();

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}
}

export default new Forms_extended;
