<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		:placeholder="field_data.placeholder"
		:disabled="field_data.type_settings?.disabled || cena_disable"
		@blur="transform_value"
		@keydown="prevent_letters_in_float"
		:maxlength="html_attributes(true)"
		:minlength="html_attributes(false)"
		type="text"
		class="form-control"
		:class="transform_class"
	>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import podlazie_text_mixin from '@/components/forms/dynamic_input_mixins/podlazie_text_mixin.js';
	import cena_text_mixin from '@/components/forms/dynamic_input_mixins/cena_text_mixin.js';
	import Associative from '@/logic/Associative.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed'],
		mixins: [podlazie_text_mixin, cena_text_mixin],
		data() {
			return {
				cena_disable: false,
			}
		},
		created() {
		},
		methods: {
			transform_value()
			{
				// remove stating/trailing whitespace
				if (typeof this.value === 'string' && this.value !== this.value.trim()) {
					this.value = this.value.trim();
				}
				// everything to uppercase
				if (this.field_data.type_settings?.transform === 'uppercase') {
					this.value = this.value.toUpperCase();
				}
				// first character upper, rest of string left alone
				else if (this.field_data.type_settings?.transform === 'capitalize') {
					this.value = this.value.charAt(0).toUpperCase() + this.value.slice(1);
				}
				// allow only 2 decimals
				if (this.field_data.value_type === 'dec') {
					let fresh = this.value.replace(/,/g, '.'); // exchange all dots for commas
					fresh = fresh.replace(/[^\d.]/g, ''); // remove everything not number or dot
					fresh = fresh.replace(/^0+/, ''); // remove leading zeros
					let index = fresh.indexOf('.'); // decimal separator position
					if (index === -1) {
						fresh = fresh + '.'; // if decimal separator not present add it
					}
					index = fresh.indexOf('.'); // refresh position
					if (index === 0) {
						fresh = '0.' + fresh; // if no number on start, expect bellow zero
					}
					index = fresh.indexOf('.'); // refresh position
					let trail = fresh.slice(index); // adjust trail only
					trail = trail.replace(/\./g, ''); // remove multiple separator occurence
					let decimals = this.field_data.type_settings?.decimal_digits || 2; // default decimal number is 2
					decimals = parseInt(decimals, 10); // dunno if they send it as string
					if (trail.length > decimals) {
						trail = trail.slice(0, decimals); // slice if over max numbers
					}
					else if (trail.length < decimals) {
						trail = trail.padEnd(decimals, '0'); // pad if under min numbers
					}
					this.value = fresh.slice(0, index+1) + trail; // set to v-model
				}
				// in step2 emit change event
				if (this.emit_name !== undefined) {
					this.$emit(this.emit_name);
				}
				// Associative
				Associative.init_associative(this.field_id, this.field_data);
			},
			prevent_letters_in_float(event)
			{
				if (this.field_data.value_type !== 'dec') {
					return;
				}
				// prevent typing letters and symbols
				let utilities = [',', '.', 'Backspace', 'Delete', 'Escape', 'Enter', 'Tab', 'ArrowRight', 'ArrowLeft', 'ArrowUp', 'ArrowDown',];
				if (
					event.ctrlKey === false && // wasnt with Controll key
					isNaN(parseInt(event.key, 10)) && // key is not number
					utilities.indexOf(event.key) === -1 // key is not utility
				) {
					event.preventDefault();
				}
				// prevent typing more than max decimal_digits
				let index = this.value.match(/,|\./)?.index;
				let trail = this.value.slice(index);
				let decimals = this.field_data.type_settings?.decimal_digits || 2; // default decimal number is 2
				if (
					index !== undefined && // has separator
					trail.length > decimals && // have max decimals
					event.ctrlKey === false && // wasnt with Controll key
					utilities.indexOf(event.key) === -1 && // wasnt utility
					event.target.selectionStart > index // tried typing after separator
				) {
					event.preventDefault();
				}
			},
			html_attributes(min_max)
			{
				// default for max is 50
				if (min_max) {
					return this.field_data.attributes?.maxlength || 50;
				}
				// default for min is 0
				else {
					return this.field_data.attributes?.minlength || 0;
				}
			},
		},
		computed: {
			transform_class()
			{
				if (this.field_data.type_settings?.transform === 'uppercase') {
					return 'text_uppercase';
				} else if (this.field_data.type_settings?.transform === 'capitalize') {
					return 'text_capitalize';
				} else {
					return '';
				}
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
