<template>
	<div class="prop_confirm" data-cy="acceptance_form" >

		<h3 class="text-primary">
			Záverečné potvrdenie
		</h3>

		<!-- vzdy pritomny marketingovy suhlas + akceptovanie elektronickej komunikacie -->
		<div class="confirm_check">
			<div class="form-check">
				<input
					name="suhlas_elekt_kom"
					id="suhlas_elekt_kom"
					v-model="checkboxes.suhlas_elekt_kom"
					true-value="1"
					false-value="0"
					type="checkbox"
					class="form-check-input"
					:class="this.invalid_checkboxes ? 'required_checkbox' : ''"
				>
				<label
					for="suhlas_elekt_kom"
					class="form-check-label"
				>
					<!-- elekt_kom_modal_trigger -->
					<button
						@click="marketing_ek_modal"
						class="btn-text text-start"
					>
						Súhlas s elektronickou komunikáciou
					</button>
				</label>
			</div>
			<div class="form-check">
				<input
					name="suhlas_marketing"
					id="suhlas_marketing"
					v-model="checkboxes.suhlas_marketing"
					true-value="1"
					false-value="0"
					type="checkbox"
					class="form-check-input"
				>
				<label
					for="suhlas_marketing"
					class="form-check-label"
				>
					<!-- marketing_modal_trigger -->
					<button
						@click="marketing_modal"
						class="btn-text text-start"
					>
						Súhlas so spracovaním osobných údajov pre účely marketingu
					</button>
				</label>
			</div>
		</div>

		<!-- pravny suhlas -->
		<div class="confirm_check">
			<p v-if="wording.paragraph">
				{{ wording.paragraph }}
			</p>
			<div class="form-check">
				<input
					name="suhlas_navrh"
					id="suhlas_navrh"
					v-model="checkboxes.suhlas_navrh"
					true-value="1"
					false-value="0"
					type="checkbox"
					class="form-check-input"
					:class="this.invalid_checkboxes ? 'required_checkbox' : ''"
				>
				<label
					for="suhlas_navrh"
					class="form-check-label"
				>
					{{ wording.label }}
				</label>
			</div>
		</div>

		<!-- navigacia a error message -->
		<div class="row form-pagination">
			<div class="col-12">
				<!-- error message -->
				<p v-show="invalid_checkboxes" class="text-accent">
					Pred pokračovaním musíte potvrdiť zvýraznené zaškrtávacie políčka
				</p>
			</div>
			<div class="col-12 col-sm-6 text-center text-sm-start order-2 order-sm-1">
				<!-- back -->
				<button
					@click="edit_user_data"
					class="btn btn-white btn-left btn-lg"
				>
					Upraviť
				</button>
			</div>
			<div class="col-12 col-sm-6 text-center text-sm-end order-1 order-sm-2">
				<!-- confirm button -->
				<button
					@click="confirm_button_press"
					v-show="!confirm_running"
					type="button"
					class="btn btn-accent btn-right btn-lg"
				>
					<span>{{ wording.span }}</span>
				</button>
				<!-- confirm loading -->
				<Bouncer_loading v-show="confirm_running" style="margin-top: 18px; margin-right: 50px;" />
			</div>
		</div>

	</div><!-- .prop_confirm -->
</template>

<script>
	import Bouncer_loading from '@/components/global/Bouncer_loading.vue';
	import Confirm_requests from '@/logic/requests/Confirm_requests.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_nav from '@/store/STATE_nav.js';

	export default {
		props: {
			mode: String, // classic_confirm | provider_agent | provider_client | sms_agent | sms_client
		},
		components: {
			Bouncer_loading,
		},
		emits: [],
		data() {
			return {
				confirm_running: false,
				invalid_checkboxes: false,
				checkboxes: {
					suhlas_elekt_kom: '0',
					suhlas_marketing: '0',
					suhlas_navrh: '0',
				},
			}
		},
		created() {
		},
		methods: {
			confirm_button_press()
			{
				// chechboxes validation
				if (this.checkboxes.suhlas_elekt_kom === '0' || this.checkboxes.suhlas_navrh === '0') {
					this.invalid_checkboxes = true;
					return;
				}
				this.invalid_checkboxes = false;

				// button action by mode
				if (this.mode === 'classic_confirm') {
					this.run_confirm(true);
				}
				else if (this.mode === 'sms_agent' || this.mode === 'provider_agent') {
					STATE_modals().store_modal_data(this.checkboxes);
					STATE_modals().open_it('Signature_agent');
				}
				else if (this.mode === 'sms_client' || this.mode === 'provider_client') {
					STATE_modals().store_modal_data({mode: this.mode, checkboxes: this.checkboxes});
					STATE_modals().open_it('Signature_client');
				}
			},
			run_confirm(add_checkboxes_except_autorun)
			{
				// agent 2nd visit doesnt send checkboxes
				let chb = {};
				if (add_checkboxes_except_autorun) {
					chb = this.checkboxes;
				}

				// loading
				this.confirm_running = true;

				// confirm
				// duplicit confirm is in Signature_client, broken DRY principle :(
				Confirm_requests.confirm_session(chb).then(
					(response) => {

						// loading
						this.confirm_running = false;

						// success
						if (
							response.status === 200 &&
							(
								response.result?.response === 'success'/*confirm*/ ||
								response.result?.state === 100/*getsessioninfo*/
							)
						) {
							STATE_core().change_step(5);
							return;
						}

						// baaad data, fallback error
						console.log('confirm simple error');
						STATE_modals().store_modal_data(response.result);
						STATE_modals().open_it('Confirm_simple_error');

					}
				);
			},
			marketing_modal()
			{
				STATE_modals().open_it('Marketing');
			},
			marketing_ek_modal()
			{
				STATE_modals().open_it('Marketing_ek');
			},
			edit_user_data()
			{
				STATE_core().change_step(3);
			},
		},
		computed: {
			wording()
			{
				// each confirm mode has different wording
				let wording = {
					classic_confirm: {
						span: 'Dokončiť',
						paragraph: '',
						label: 'Uvedený návrh na uzatvorenie poistnej zmluvy som si prečítal(a) a súhlasím s ním. Týmto prehlasujem, že som bol oboznámený(á) s vyššie uvedenou predzmluvnou dokumentáciou, obsahu som porozumel(a) a týmto vyjadrujem súhlas.',
					},
					sms_agent: {
						span: 'Odoslať návrh emailom',
						paragraph: 'Pre dokončenie zmluvy o poskytnutí finančnej služby je potrebné potvrdenie klienta formou SMS autorizácie.',
						label: 'Totožnosť klienta som overil a súhlasím s odoslaním dokumentácie na email klienta, aby dokončil autorizáciu.',
					},
					sms_client: {
						span: 'Vyžiadať SMS kód',
						paragraph: 'Pre dokončenie zmluvy o poskytnutí finančnej služby je potrebné potvrdenie cez SMS kód - kliknutím na tlačitko "vyžiadať sms kód".',
						label: 'Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o zaslanie SMS kódu na moje telefónne číslo.',
					},
					provider_agent: {
						span: 'Odoslať dokumentáciu emailom',
						paragraph: 'Pre akceptáciu predzmluvnej dokumentácie je potrebné potvrdenie klienta formou SMS autorizácie a pre dokončenie zmluvy je potrebné doplniť chýbajúce údaje na webe providera a zmluvu podpísať.',
						label: 'Totožnosť klienta som overil a súhlasím s odoslaním dokumentácie na email klienta, aby dokončil autorizáciu. Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o presmerovanie na stránku providera.',
					},
					provider_client: {
						span: 'Vyžiadať SMS kód',
						paragraph: 'Pre akceptáciu predzmluvnej dokumentácie je potrebné potvrdenie klienta formou SMS autorizácie a pre dokončenie zmluvy je potrebné doplniť chýbajúce údaje na webe providera a zmluvu podpísať.',
						label: 'Oboznámil som sa s predzmluvnou dokumentáciou, nemám žiadne výhrady s jej obsahom a žiadam o zaslanie SMS kódu na moje telefónne číslo.',
					},
				};

				// internal error
				if (!wording.hasOwnProperty(this.mode)) {
					return {};
				}

				// pick right one
				return wording[this.mode];
			},
			requested_direction()
			{
				return STATE_nav().get_requested_direction();
			},
		},
		watch: {
			requested_direction()
			{
				if (this.requested_direction === null) {
					return;
				}
				if (this.requested_direction === 'backward') {
					STATE_nav().reset_requested_direction();
					this.edit_user_data();
				}
			},
		},
	}
</script>
