import Request_service from '@/logic/Request_service.js';
import Functions from '@/logic/Functions.js';
import STATE_core from '@/store/STATE_core.js';

/**
 * Get_flow_form
*/
class Get_flow_form
{
	/**
	 * osmapi_get_form
	*/
	osmapi_get_form(action)
	{
		let requestParams = {
			action_name: 'get-form',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'osmapi/get-form/' + STATE_core().get_session_token(),
		};

		var array = [];
		array.push('source=' + action.functionParams.source); // group, forms
		array.push('source-id=' + action.functionParams['source-id']);
		array.push('action-id=' + action.id);
		// array.push('global=0');
		array.push('json=1');
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}

	/**
	 * osmapi_validate_form
	*/
	osmapi_validate_form(action)
	{
		let requestParams = {
			action_name: 'validate-form',
			method: 'POST',
			url: STATE_core().endpoint_url() + 'osmapi/validate-form/' + STATE_core().get_session_token(),
			body: Functions.objectify_user_input(), // doesnt use serialize unlike form_app validate
			content_type: 'application/json',
		};

		var array = [];
		array.push('source=' + action.functionParams.source); // group, forms
		array.push('source-id=' + action.functionParams['source-id']);
		array.push('action-id=' + action.id);
		// array.push('global=0');
		array.push('json=1');
		array.push('without_form=1');
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().catch((error) => { throw error; });
	}
}

export default new Get_flow_form;
