<template>
	<!-- tooltip -->
	<div
		v-if="tooltip_text"
		class="ttip"
		:class="tooltip_class"
	>

		<b v-if="tooltip_type === 'info'" >?</b>
		<b v-else-if="tooltip_type === 'warning'" >!</b>
		<span v-else-if="tooltip_type === 'cost'" >&nbsp;(€)</span>
		<b v-else >bad tooltip type</b>

		<span
			v-html="tooltip_text"
			class="ttip_text"
			:class="tooltip_width_class"
		></span>

	</div>
</template>

<script>
	export default {
		props: {
			tooltip_text: String,
			tooltip_type: String, // info | cost | warning
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			tooltip_width_class()
			{
				return this.tooltip_text.length < 30 ? 'shortest' : this.tooltip_text.length < 40 ? 'shorter' : this.tooltip_text.length < 55 ? 'short' : '';
			},
			tooltip_class()
			{
				return this.tooltip_type === 'warning' ? 'pr_warning' : this.tooltip_type === 'cost' ? 'ttip-top' : '';
			},
		},
		watch: {
		},
	}
</script>
