import STATE_core from '@/store/STATE_core.js';
import STATE_params from '@/store/STATE_params.js';
import Constants from '@/logic/Constants.js';

/**
 * Analytics
*/
class Analytics
{
	/**
	 * init_analytics
	*/
	init_analytics()
	{
		if (!STATE_core().enable_analytics()) {
			return;
		}

		/************************************************************************************/
		/* remove this comment */
		/* add conditions where to run it */
		/* like only on production */
		/* or only for certain receivers */
		/************************************************************************************/
		console.log('analytics on');

		// <!-- Google Tag Manager -->
		/*<script>*/(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':
		new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],
		j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src=
		'https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);
		})(window,document,'script','dataLayer','GTM-NXH9X7RQ');/*</script>*/
		// <!-- End Google Tag Manager -->
	}

	/**
	 * send_event
	*/
	send_event(event_name)
	{
		if (!STATE_core().enable_analytics()) {
			return;
		}

		window.dataLayer = window.dataLayer || [];
		window.dataLayer.push(
			{
				event: event_name,
				version: STATE_core().give_app_version(),
				session_token: STATE_core().get_session_token(),
				endpoint_type: STATE_core().get_endpoint_type(),
				backend_type: STATE_core().get_backend_type(),
				group_id: STATE_core().product_group_id() + ': ' + Constants.group_names()[STATE_core().product_group_id()],
				step: STATE_core().current_step() + ': ' + Constants.step_names(STATE_core().current_step()),
				agent_code: STATE_params().get_agent_code(),
				selected_product: STATE_params().get_selected_product(),
				purchase: STATE_core().current_step() > 4 && STATE_core().current_step() < 10 ? STATE_params().get_selected_product()?.product_expected_price : '',
				filtered_products: STATE_params().get_filtered_products(),
				receiver: STATE_params().get_user_input('receiver') + ': ' + Constants.receiver_names(STATE_params().get_user_input('receiver')),
				host_url: window.location?.href,
			}
		);
	}
}

export default new Analytics;
