import STATE_params from '@/store/STATE_params.js';

/**
 * Associative
*/
class Associative
{
	/**
	 * init_associative
	*/
	init_associative(field_id, field_data)
	{
		// DISABLE IN MASTER
		// return;
		// if (this.field_data.input_settings?.associative === undefined) {
		// 	return;
		// }
		if (field_id !== 'zp_osoba1_ps_kch') {
			return;
		}
		let settings = [
			{
				if: '$zp_osoba1_ps_kch <= $zp_osoba1_ps_kch_1',
				set: '$zp_osoba1_ps_kch',
				target: ['zp_osoba1_ps_kch_1',],
			},
		];
		for (let index in settings) {
			// if
			let string = this.replace_params(settings[index].if);
			if (!this.eval_associative(string)) {
				continue;
			}
			// value
			let value = this.replace_params(settings[index].set);
			value = this.eval_associative(value);
			// set value
			STATE_params().set_form_value(settings[index].target, value);
		}
	}

	/**
	 * replace_params
	*/
	replace_params(string)
	{
		let match = string.match(/\$[a-z|_|0-9]*/g);
		if (match === null) {
			return string;
		}
		for (let index in match) {
			let id = match[index].replace('$', '');
			let value = STATE_params().get_user_input(id) || '';
			if (isNaN(value) || value === '') {
				value = "'" + value + "'";
			}
			string = string.replace(match[index], value);
		}
		return string;
	}

	/**
	 * eval_associative
	*/
	eval_associative(string)
	{
		try {
			return eval(string);
		} catch (error) {
			console.log('associative error:', string);
			return false;
		}
	}
}

export default new Associative;
