import { defineStore } from 'pinia';

export default defineStore(
	'STATE_flow',
	{
		state: () => {
			return {
				workflow_status: {
					actions: [],
					finished: false,
					internal: false,
					step: -1,
					stepState: 'before_init',
				},
				workflow_name: null, // null | string
			};
		},
		getters: {
			// status
			get_workflow_actions()
			{
				return () => {
					return this.workflow_status.actions;
				}
			},
			get_workflow_finished()
			{
				return () => {
					return this.workflow_status.finished;
				}
			},
			get_workflow_internal()
			{
				return () => {
					return this.workflow_status.internal;
				}
			},
			get_workflow_step()
			{
				return () => {
					return this.workflow_status.step;
				}
			},
			get_workflow_step_state()
			{
				return () => {
					return this.workflow_status.stepState;
				}
			},
			// name
			get_workflow_name()
			{
				return () => {
					return this.workflow_name;
				}
			},
		},
		actions: {
			// status
			set_workflow_status(status)
			{
				if (
					typeof status.actions !== 'object' ||
					typeof status.finished !== 'boolean' ||
					typeof status.internal !== 'boolean' ||
					typeof status.step !== 'number' ||
					typeof status.stepState !== 'string' ||
					! ['pending', 'processing', 'success', 'error'].includes(status.stepState)
				) {
					console.dir('status: ' + typeof status, status);
					throw new TypeError('workflow_status invalid');
				}
				this.workflow_status = status;
			},
			clear_workflow_status()
			{
				this.workflow_status = {
					actions: [],
					finished: false,
					internal: false,
					step: -1,
					stepState: 'before_init',
				};
				this.workflow_name = null;
			},
			// name
			set_workflow_name(name)
			{
				this.workflow_name = name;
			},
		},
	}
);
