<template>
	<Info />
	<div :class="[find_template]" data-cy="nodered_flow">
		<div class="row">
			<Actions_loader v-for="(value, key) in step_actions" :action="value" @requested_step="next_step" />
		</div>
	</div>
</template>

<script>
	import Info from '@/components/form_app/nav/Info.vue';
	import Flow_handling from '@/logic/flow_only/Flow_handling.js';
	import Actions_loader from '@/components/flow/Actions_loader.vue';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_flow from '@/store/STATE_flow.js';

	export default {
		props: {
		},
		components: {
			Info,
			Actions_loader,
		},
		emits: [],
		data() {
			return {
				status_repeat_frequency: 3 * 1000, // 3 seconds
				status_repeats_count: 0,
				status_repeats_limit: 10,
			}
		},
		created() {
			this.init_session();
		},
		methods: {
			init_session()
			{
				// restore
				if (!STATE_core().is_fresh_session()) {
					this.status();
					return;
				}
				// new
				Flow_handling.flow_init_session().then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						this.what_to_do_next();
					}
				);
			},
			next_step(requested = undefined)
			{
				this.status_repeats_count = 0;
				Flow_handling.flow_next_step(requested).then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						this.what_to_do_next();
					}
				);
			},
			status()
			{
				Flow_handling.flow_status().then(
					(response) => {
						if (response.status !== 200) {
							return;
						}
						this.what_to_do_next();
					}
				);
			},
			what_to_do_next()
			{
				var state = STATE_flow().get_workflow_step_state();
				var internal = STATE_flow().get_workflow_internal();
				var finished = STATE_flow().get_workflow_finished();

				if (finished === true) {
					return;
				}
				else if (state === 'success') {
					this.next_step();
				}
				else if (internal === true) {
					if (this.status_repeats_count > this.status_repeats_limit) {
						console.log('reached status repeat limit, stopping requests');
						return;
					}
					this.status_repeats_count++;
					setTimeout(
						function() {
							this.status();
						}.bind( this ),
						this.status_repeat_frequency
					);
				}
			},
		},
		computed: {
			step_actions()
			{
				var all_actions = STATE_flow().get_workflow_actions();
				var without_template = [];
				var i, len = all_actions.length;
				for ( i = 0; i < len; i++ ) {
					if (all_actions[i].function !== 'template') {
						// without_template[all_actions[i].functionPosition.order] = all_actions[i];
						without_template.push(all_actions[i]);
					}
				}
				return without_template;
			},
			find_template()
			{
				var actions = STATE_flow().get_workflow_actions();
				var i, len = actions.length;
				for ( i = 0; i < len; i++ ) {
					if (actions[i].function === 'template') {
						return actions[i].functionParams.type;
					}
				}
				return null;
			},
		},
		watch: {
		},
	}
</script>

<style scoped lang="scss">
	// template names
	.review {
		background-color: #eef9fb;
		padding: 16px;
	}
	.final {
		background-color: #3cb371;
	}
</style>
