<template>
	<Modal_body :close="true" :title="text.title" class="modal-cleaning_session text-center">

		<p>{{ text.subtitle }}</p>
		<button
			v-if="text.yes !== null"
			@click="clear"
			type="button"
			class="btn btn-primary btn-w80 me-2"
		>
			{{ text.yes }}
		</button>
		<button
			@click="cancel"
			type="button"
			class="btn btn-secondary btn-w80 ms-2"
		>
			{{ text.no }}
		</button>

	</Modal_body>
</template>

<script>
	import Modal_body from '@/components/modals/parts/Modal_body.vue';
	import STATE_modals from '@/store/STATE_modals.js';
	import Functions from '@/logic/Functions.js';
	import STATE_params from '@/store/STATE_params.js';
	import STATE_core from '@/store/STATE_core.js';

	export default {
		props: {
		},
		components: {
			Modal_body,
		},
		data() {
			return {
			}
		},
		created() {
			// aprove modal on Enter key
			document.addEventListener('keyup', this.clear);
			// save receiver
			this.receiver = STATE_params().get_user_input('receiver');
			this.step = STATE_core().current_step();
		},
		beforeUnmount() {
			// kill the listener
			document.removeEventListener('keyup', this.clear);
		},
		methods: {
			clear(event)
			{
				if (event?.keyCode === undefined/*click on button*/ || event.keyCode === 13/*Enter*/) {
					if (this.receiver === 3/*ums*/) {
						let url = 'https://test.ums.jdsoftware.sk/clone/' + STATE_core().get_session_token();
						window.location.href = url;
					}
					Functions.clear_session();
				}
			},
			cancel()
			{
				STATE_modals().close_them_all();
			},
		},
		computed: {
			text()
			{
				let text = {
					runner_14: {
						title: 'Naozaj chcete vymazať údaje?',
						subtitle: 'Stratíte všetky údaje, ktoré ste doteraz vyplnili.',
						yes: 'Áno',
						no: 'Nie',
					},
					runner_56: {
						title: 'Vytvoriť nové porovnanie?',
						subtitle: 'Zatvoríme rekapituláciu a otvoríme nové porovnanie.',
						yes: 'Nové porovnanie',
						no: 'Zrusiť',
					},
					ums_14: {
						title: 'Naozaj chcete vymazať údaje?',
						subtitle: 'Pre vytvorenie nového porovnania pokračujte voľbou Nové porovnanie.',
						yes: 'Nové porovnanie',
						no: 'Zrusiť',
					},
					ums_56: {
						title: 'Vytvoriť nové porovnanie',
						subtitle: 'Pre vytvorenie nového porovnania pokračujte voľbou Nové porovnanie.',
						yes: 'Nové porovnanie',
						no: 'Zrusiť',
					},
					other: {
						title: 'Nie je možné zmazať údaje',
						subtitle: 'Pre vytvorenie nového porovnania prosím kliknite na odkaz vo Vašej aplikácii.',
						yes: null,
						no: 'Zrusiť',
					},
					empty: {title: '', subtitle: '', yes: '', no: '',},
				};
				if ([1/*fp*/, 2/*np*/, 3/*ums*/, 4/*pg*/,].indexOf(this.receiver) === -1) {
					return text.other;
				}
				if (this.receiver === 3 && this.step < 5) {
					return text.ums_14;
				}
				if (this.receiver === 3 && this.step > 4) {
					return text.ums_56;
				}
				if (this.step < 5) {
					return text.runner_14;
				}
				if (this.step > 4) {
					return text.runner_56;
				}
				return text.empty; // fallback, should not happen
			},
		},
	}
</script>
