<template>
	<div
		@click="did_click_outside"
		class="vue_modal"
		:class="huge ? 'huge' : ''"
		data-cy="modal"
	>
		<div class="modal-content">
			<div class="modal-header">
				<h4>
					{{ title }}
				</h4>
				<span
					v-if="close"
					@click="close_me"
					class="i_close"
				>
				</span>
			</div>
			<div class="modal-body">

					<slot></slot>

			</div>
		</div>
	</div>
</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';

	export default {
		props: {
			close: Boolean,
			title: String,
			huge: Boolean, // optional
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
			// close modal on Esc
			document.addEventListener('keyup', this.close_me);
		},
		beforeUnmount() {
			// kill the listener
			document.removeEventListener('keyup', this.close_me);
		},
		methods: {
			close_me(event)
			{
				if (
					// allow closing
					this.close &&
					(
						// clicked outside OR close button
						event?.type === 'click' ||
						// escape on keyboard
						(event?.type === 'keyup' && event?.keyCode === 27/*Esc*/)
					)
				) {
					// close modal
					STATE_modals().close_them_all();
				}
			},
			did_click_outside(event)
			{
				// act only when clicked to outer modal background
				if (event.target.classList.contains('vue_modal')) {
					this.close_me(event);
				}
			},
		},
		computed: {
		},
		watch: {
		},
	}
</script>
