<template>

	<!-- not shown in technical steps like error or maintenance -->
	<div
		v-if="not_shown_in_technical_steps"
		class="step_progress_alt"
	>
		<div class="step_progress_alt_steps" data-cy="progress_bar">

			<div
				:class="step_info_class(1)"
				class="step_info"
			>
				<div class="step_number">1.</div>
				<div class="step_title">Parametre<br>poistenia</div>
				<div class="step_tabs" v-if="step === 1" >
					<span
						v-for="index in tab_count"
						:class="step_tab_class(index)"
						v-show="tab_not_empty(index)"
					></span>
				</div>
			</div>
			<div
				:class="step_info_class(2)"
				class="step_info"
			>
				<div class="step_number">2.</div>
				<div class="step_title">Porovnanie<br>ponúk</div>
			</div>
			<div
				:class="step_info_class(3)"
				class="step_info"
			>
				<div class="step_number">3.</div>
				<div class="step_title">Doplňujúce<br>údaje</div>
				<div class="step_tabs" v-if="step === 3" >
				<span
					v-for="index in tab_count"
					:class="step_tab_class(index)"
					v-show="tab_not_empty(index)"
				></span>
			</div>
			</div>
			<div
				:class="step_info_class(4)"
				class="step_info"
			>
				<div class="step_number">4.</div>
				<div class="step_title">Rekapitulácia</div>
			</div>
			<div
				:class="step_info_class(5)"
				class="step_info"
			>
				<div class="step_number">5.</div>
				<div class="step_title">Dokončenie</div>
			</div>

		</div>
	</div>

</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_forms from '@/store/STATE_forms.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			show_back: Boolean,
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			step_info_class(int)
			{
				if (int < this.step) {
					return 'completed';
				}
				else if (int === this.step) {
					return 'current';
				}
				else {
					return '';
				}
			},
			step_tab_class(int)
			{
				if (int < STATE_forms().get_tab_page().current) {
					return 'completed';
				}
				else if (int === STATE_forms().get_tab_page().current) {
					return 'current';
				}
				else {
					return '';
				}
			},
			tab_not_empty(tab)
			{
				let active_tab = Functions.active_tab(tab);
				return ! (active_tab === undefined/*missing tab*/ || Object.keys(active_tab).length === 0/*all hidden*/);
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			tab_count()
			{
				return STATE_forms().get_tab_page().keys;
			},
			not_shown_in_technical_steps()
			{
				return this.step < 10;
			},
		},
		watch: {
		},
	}
</script>
