<template>
	<select
		:id="field_id"
		:name="field_id"
		v-model="value"
		:disabled="is_disabled"
		class="form-control"
	>
		<option
			v-for="(item, key) in field_data.enum"
			:value="option_value(item)"
		>
			{{ option_label(item) }}
		</option>
	</select>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import combined_select_mixin from '@/components/forms/dynamic_input_mixins/combined_select_mixin.js';
	import Functions from '@/logic/Functions.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		mixins: [combined_select_mixin],
		data() {
			return {
				loading: false,
			}
		},
		created() {
		},
		methods: {
			option_value(item)
			{
				return Functions.enum_value(item);
			},
			option_label(item)
			{
				return Functions.enum_label(item);
			},
		},
		computed: {
			is_disabled()
			{
				if (this.loading === true) {
					return true;
				}
				return this.field_data.type_settings?.disabled;
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>
