import STATE_core from '@/store/STATE_core.js';

/**
 * Constants
*/
class Constants
{
	/**
	 * email_address
	*/
	email_address()
	{
		if (STATE_core().is_partners_skin()) {
			return 'poistenie@partnersgroup.sk';
		}
		return 'najpoistenie@najpoistenie.sk';
	}


	/**
	 * phone_number
	*/
	phone_number(tech_support)
	{
		if (tech_support) {
			// technical support for agents
			return '+421 905 497 554';
		}
		// common public users
		return '+421 917 369 449';
	}


	/**
	 * group_names
	*/
	group_names()
	{
		return {
			1: 'Povinné zmluvné poistenie',
			2: 'Havarijné poistenie',
			3: 'Hypotekárny úver',
			4: 'Životné poistenie',
			5: 'GAP poistenie finančnej straty',
			6: 'Investície',
			7: 'Cestovné poistenie',
			8: 'Poistenie zodpovednosti zamestnanca',
			9: 'Poistenie majetku - byt, dom, domácnosť',
			10: 'Poistenie podnikateľov',
			72: 'Sedadlá',
			73: 'Zdravotné poistenie cudzincov',
			81: 'Zdravotné poistenie',
			82: 'Test',
			85: 'Starobné dôchodkové sporenie', // dss
			86: 'Doplnkové dôchodkové sporenie', // dds
		};
	}


	/**
	 * step_names
	*/
	step_names(int)
	{
		let steps = {
			'-1': 'Cookie manager',
			0: 'Preposlaná ponuka',
			1: 'Vstupný formulár',
			2: 'Výber produktov',
			3: 'Údaje klienta',
			4: 'Návrh zmluvy',
			5: 'Potvrdenie zmluvy',
			6: 'Návrat z platby',
			88: 'Testovací dizajn',
			99: 'Testovanie logiky',
			111: 'Cookie manager',
			404: 'Error stránka',
			503: 'Údržba',
			'-77': 'Čistenie session',
		};
		return steps[int];
	}


	/**
	 * receiver_names
	*/
	receiver_names(int)
	{
		let receivers = {
			1: 'Finportal',
			2: 'Najpoistenie',
			3: 'UMS',
			4: 'Partners group',
			5: 'Centrum poistenia',
			6: 'Realfin',
		};
		return receivers[int];
	}
}

export default new Constants;
