<template>
	<div class="input-group">
		<input
			:id="field_id"
			:name="field_id"
			v-model="value"
			@keyup="run_search"
			:placeholder="field_data.placeholder"
			type="text"
			maxlength="17"
			class="form-control input-search"
		>
		<div class="input-group-append">
			<span
				@click="run_search"
				class="input-search-icon"
			></span>
		</div>
		<span
			v-show="error_message"
			class="search_input_error ms-2"
		>
			{{ error_message }}
		</span>
	</div>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Functions from '@/logic/Functions.js';
	import Search_requests from '@/logic/requests/Search_requests.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
				error_message: '',
			}
		},
		created() {
		},
		methods: {
			run_search(event)
			{
				// on click event, or any key other than Enter
				if (event.type === 'keyup' && event.keyCode !== 13) {
					return;
				}

				// transform
				this.value = Functions.keep_only_letters_and_numbers(this.value);
				this.value = this.value.toUpperCase();

				// fe validation & define search_type
				let search_type = '';
				if (Functions.ends_with(this.field_id, 'company_search'))
				{
					search_type = 'ico';
					if (this.value.length !== 8 || !Functions.contains_only_digits(this.value)) {
						this.create_search_error('Zadajte 8 miestne číslo.');
						return;
					}
				}
				else if (Functions.ends_with(this.field_id, 'auto_search'))
				{
					if (
						(this.value.length === 7 && Functions.first_two_are_letters(this.value))
						||
						(this.value.length === 6 && Functions.first_one_letter_last_three_numbers(this.value))
					) {
						search_type = 'spz';
					} else if (this.value.length >= 5 && this.value.length <= 17) {
						search_type = 'vin';
					} else {
						this.create_search_error('Zadaných málo znakov. Zadajte v správnom tvare alebo vyplňte údaje o vozidle nižšie.');
						return;
					}
				}
				else {
					console.log('running on wrong ID');
					return;
				}

				// request data
				Promise.resolve().then(
					() => {
						if (search_type === 'spz' || search_type === 'vin') {
							return Search_requests.car_data_search(search_type, this.value);
						} else if (search_type === 'ico') {
							return Search_requests.ico_info(this.value);
						}
					}
				// under certain conditions try other api
				).then(
					(response) => {
						if (
							response.status === 200 &&
							response.result.data.length === 0 &&
							(search_type === 'spz' || search_type === 'vin') &&
							(this.value.length === 6 || this.value.length === 7)
						) {
							search_type = search_type === 'spz' ? 'vin' : 'spz';
							return Search_requests.car_data_search(search_type, this.value);
						} else {
							return Promise.resolve(response);
						}
					}
				// fill data, or show error
				).then(
					(response) => {
						if (response.status === 200 && response.result.data.length !== 0) {
							STATE_params().set_multiple_form_values(response.result.data);
							return;
						}
						if (search_type === 'spz' || search_type === 'vin') {
							this.create_search_error('Vozidlo sa nenašlo v databáze vozidiel. Zadajte údaje o vozidle nižšie.');
						} else if (search_type === 'ico') {
							this.create_search_error('Vyhľadávanie nebolo úspešné.');
						}
					}
				);
			},
			create_search_error(message)
			{
				// display error inside input and hide in few seconds
				this.error_message = message;
				setTimeout(
					() => { this.error_message = '' },
					6000 // 6s
				);
			},
		},
		computed: {
			value:
			{
				get() {
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
				},
			},
		},
		watch: {
		},
	}
</script>
