<template>
	<!-- DISABLE IN MASTER -->
	<small v-if="true" >
		<span v-for="(value, key) in field_data.validators">
			<strong>{{ value.type }}</strong> {{ value.validation }}
			<br>
		</span>
		<span v-for="(value, key) in reports" style="color: purple; font-size: 13px;">
			<em>{{ value }}</em>
			<br>
		</span>
	</small>
</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Validator_service from '@/logic/Validator_service.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
		},
		components: {
		},
		emits: [],
		data() {
			return {
				reports: [],
			}
		},
		created() {
		},
		methods: {
		},
		computed: {
			input_changed()
			{
				// DISABLE IN MASTER
				// return null;
				// watched for input value change
				return STATE_params().get_form_value(this.field_id);
			},
		},
		watch: {
			input_changed()
			{
				this.reports = [];
				for (let order in this.field_data.validators) {
					let type = this.field_data.validators[order].type;
					let validation = this.field_data.validators[order].validation;
					let messages = this.field_data.validators[order].messages;
					if (type === 'Application_Model_Validators_Integer')
					{
						if (isNaN(this.input_changed)) {
							this.reports.push(messages);
						}
					}
					else if (type === 'Zend_Validate_Regex')
					{
						let regex = new RegExp(validation.substring(1, validation.length-1));
						if (!regex.test(this.input_changed)) {
							this.reports.push(messages);
						}
					}
					else if (type === 'Application_Model_Validators_ValueRevalidation')
					{
						if (Validator_service.revalidation(validation)) {
							this.reports.push(messages);
						}
					}
					else if (type === 'Application_Model_Validators_FullString') {
						let regex = new RegExp(/[^a-zA-Z0-9ľĺĹščćťžýáíéňäúďřŕŔóÓôěĚöÖüÜĽŠČŤŽÝÁÍÉŇÄÚĎŘŮůőŐűŰ+--–\\)(&@§€#\/%?=!:_,.; \r\n\t\s]/u);
						if (regex.test(this.input_changed)) {
							this.reports.push(messages);
						}
					}
					// Application_Model_Validators_Email - not yet implemented
					// Application_Model_Validators_Decimal - already implemented in decimal formatting
					/*
					else
					{
						console.log('unknown validator type: ' + type);
					}*/
				}
			},
		},
	}
</script>
