import Request_service from '@/logic/Request_service.js';
import STATE_core from '@/store/STATE_core.js';
import STATE_flow from '@/store/STATE_flow.js';

/**
 * Flow_handling
*/
class Flow_handling
{
	/**
	 * flow_init_session
	*/
	flow_init_session()
	{
		let requestParams = {
			action_name: 'init',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'osmapp/init/' + STATE_core().get_flow_id(),
		};

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				if (response.status !== 200) {
					return response;
				}
				STATE_core().set_session_token(response.result?.data?.session);
				STATE_flow().set_workflow_status(response.result?.data?.workflowStatus);
				STATE_flow().set_workflow_name(response.result?.data?.workflow);
				return response;

			}
		).catch(
			(error) => { throw error; }
		);
	}

	/**
	 * flow_next_step
	*/
	flow_next_step(requested)
	{
		var delay = 0; // seconds, can be used for debug

		let requestParams = {
			action_name: 'nextstep',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'osmapp/nextstep/' + STATE_core().get_session_token(),
		};

		var array = [];
		if (requested !== undefined) {
			array.push('step=' + requested);
		}
		if (delay > 0) {
			array.push('&delay=' + (delay * 1000));
		}
		array.push('with-session-data=1');
		if (STATE_core().get_endpoint_type() !== 'prod') {
			array.push('debug-info=1');
		}
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				if (response.status !== 200) {
					return response;
				}
				STATE_flow().set_workflow_status(response.result?.data?.workflowStatus);
				STATE_flow().set_workflow_name(response.result?.data?.workflow);
				return response;

			}
		).catch(
			(error) => { throw error; }
		);
	}

	/**
	 * flow_status
	*/
	flow_status()
	{
		let requestParams = {
			action_name: 'status',
			method: 'GET',
			url: STATE_core().endpoint_url() + 'osmapp/status/' + STATE_core().get_session_token(),
		};

		var array = [];
		array.push('with-session-data=1');
		if (STATE_core().get_endpoint_type() !== 'prod') {
			array.push('debug-info=1');
		}
		if (array.length > 0) {
			requestParams.url += '/?' + array.join('&');
		}

		let req = new Request_service;
		return req.prepare_params(requestParams).handle_response().then(
			(response) => {

				if (response.status !== 200) {
					return response;
				}
				STATE_flow().set_workflow_status(response.result?.data?.workflowStatus);
				STATE_flow().set_workflow_name(response.result?.data?.workflow);
				return response;

			}
		).catch(
			(error) => { throw error; }
		);
	}
}

export default new Flow_handling;
