<template>
	<div class="d-flex align-items-stretch form_summary" data-cy="summary">

		<!-- udaje -->
		<div class="w-100 data_summary">
			<strong>Vaše údaje:</strong>
			{{ user_data }}
		</div>

		<!-- back button -->
		<div class="d-flex flex-shrink-1 data_edit">
			<button
				@click="go_back"
				class="btn-text i_anchor my-auto"
			>
				<span class="i_edit"></span>
				<span class="d-none d-sm-inline">upraviť</span>
			</button>
		</div>

	</div>
</template>

<script>
	import STATE_core from '@/store/STATE_core.js';
	import STATE_nav from '@/store/STATE_nav.js';

	export default {
		props: {
			user_data: String,
		},
		components: {
		},
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			go_back()
			{
				STATE_core().change_step(1);
			},
		},
		computed: {
			requested_direction()
			{
				return STATE_nav().get_requested_direction();
			},
		},
		watch: {
			requested_direction()
			{
				if (this.requested_direction === null) {
					return;
				}
				if (this.requested_direction === 'backward') {
					STATE_nav().reset_requested_direction();
					this.go_back();
				}
			},
		},
	}
</script>
