import { defineStore } from 'pinia';

export default defineStore(
	'STATE_nav',
	{
		state: () => {
			return {
				requested_direction: null, // null | string 'backward' | string 'forward'
			};
		},
		getters: {
			// direction
			get_requested_direction()
			{
				return () => {
					return this.requested_direction;
				}
			},
		},
		actions: {
			// direction
			set_requested_direction(direction)
			{
				this.requested_direction = direction;
			},
			reset_requested_direction()
			{
				this.requested_direction = null;
			},
		},
	}
);
