import STATE_params from '@/store/STATE_params.js';

/**
 * Validator_service
*/
class Validator_service
{
	// validation = 'when(true,$hmotnost<=3500,$hmotnost>0)';
	// validation = 'logic_or($hmotnost>0,$vykon>0,$objem>$vykon)';
	// validation = 'logic_and($hmotnost>0,$vykon>0,$objem>$vykon)';
	// validation = 'in($auto_typ,10,23)';
	// validation = 'when(in($auto_typ,10,23),$hmotnost<=3500,$hmotnost>0)';

	/**
	 * revalidation
	*/
	revalidation(validation)
	{
		let condition = this.splitter(validation);

		return !this.compare(condition);
	}

	/**
	 * splitter
	*/
	splitter(validation)
	{
		// desctruct, separate by first '(' and last ')'
		let destruct = validation.match(/(.*?)\((.*)\)/);
		// fallback, always valid
		if (destruct === null) {
			return true;
		}

		let func = destruct[1];
		let string = destruct[2];

		// split by comma, not in brackets
		let parts = [];
		let level = 0;
		let current = '';
		for (let position in string) {
			let char = string[position];
			if (char === '(') {
				level++;
			} else if (char === ')') {
				level--;
			}
			if (char === ',' && level === 0) {
				parts.push(current);
				current = '';
				continue;
			}
			current += char;
			if (position == string.length-1) {
				parts.push(current);
			}
		}

		// recursive deeper levels
		for (let index in parts) {
			if (parts[index].indexOf('(') > -1) {
				parts[index] = this.splitter(parts[index]);
			}
		}

		return {
			[func]: parts,
		};
	}

	/**
	 * compare
	*/
	compare(condition)
	{
		for (let func in condition) {

			let params = condition[func];

			// deeper recursion
			for (let index in params) {
				if (typeof params[index] === 'object') {
					params[index] = this.compare(params[index]) ? 'true' : 'false';
				}
			}

			// when()
			if (func === 'when')
			{
				let success = this.go_val(params[0]);
				if (success) {
					success = params[1];
				} else {
					success = params[2];
				}
				success = this.go_val(success);
				return success;
			}
			// in()
			else if (func === 'in')
			{
				let value = this.replace_value(params[0]);
				delete params[0];
				let success = params.indexOf(value) > -1;
				return success;
			}
			// logic_and()
			else if (func === 'logic_and')
			{
				let success = true;
				for (let index in params) {
					if (this.go_val(params[index]) === false) {
						success = false;
						break;
					}
				}
				return success;
			}
			// logic_or()
			else if (func === 'logic_or')
			{
				let success = false;
				for (let index in params) {
					if (this.go_val(params[index]) === true) {
						success = true;
						break;
					}
				}
				return success;
			}
			// other functions are always valid
			else {
				return true;
			}

		} // endfor func in condition
	}

	/**
	 * go_val
	*/
	go_val(string)
	{
		string = this.replace_value(string);
		try {
			return eval(string);
		} catch (error) {
			console.log('validators error:', string);
			return false;
		}
	}

	/**
	 * replace_value
	*/
	replace_value(string)
	{
		// fix bad comparing
		string = string.replace(/=>/g, '>=');
		string = string.replace(/=</g, '<=');
		string = string.replace(/=/g, '==');
		string = string.replace(/>==/g, '>=');
		string = string.replace(/<==/g, '<=');
		string = string.replace(/<>/g, '!=');

		// call_params id is starting with $, consiting of letter, numbers, underscores
		let match = string.match(/\$[a-z|_|0-9]*/g);

		if (match === null) {
			return string;
		}

		// replace by call_params value from state
		for (let index in match) {
			let id = match[index].replace('$', '');
			let value = STATE_params().get_user_input(id) || '';
			if (isNaN(value) || value === '') {
				value = "'" + value + "'";
			}
			string = string.replace(match[index], value);
		}

		return string;
	}
}

export default new Validator_service;
