import Functions from '@/logic/Functions.js';
import STATE_core from '@/store/STATE_core.js';
import Show_hide from '@/logic/Show_hide.js';
import STATE_params from '@/store/STATE_params.js';

/**
 * Product_helpers
*/
class Product_helpers
{
	/**
	 * sort_icons
	*/
	sort_icons(icons)
	{
		let parsed = [];
		let sorted = [];
		for (let key in icons) { if (!icons.hasOwnProperty(key)) { continue; }
			if (
				key === '_isGroup' ||
				key.indexOf('_detail') !== -1 ||
				key.indexOf('_text') !== -1 ||
				icons[key].hide_in_result === '1' ||
				icons[key].result === '0'
			) {
				continue;
			}
			let nice_name = key.replace('_full', '');
			let icon = {
				title: icons[key].formula_name + (icons[nice_name + '_text']?.result ? '<b>' + icons[nice_name + '_text']?.result + '</b>' : ''),
				tooltip: icons[nice_name + '_detail']?.result,
				order: parseInt(icons[key].order, 10),
				class: nice_name + (icons[key].result === '1' ? '-ano' : '-nie') + (key.indexOf('_full') > -1 ? ' full' : ''), // '-nie' for result === '2'
			};
			parsed.push(icon);
		}
		while (parsed.length > 0) {
			var lowest = this.find_key_with_lowest_order(parsed);
			sorted.push(parsed[lowest]);
			parsed.splice(lowest, 1);
		}
		return sorted;
	}

	find_key_with_lowest_order(array)
	{
		var key = 0;
		var i = 0;
		var len = array.length;
		while (i < len) {
			if (array[key].order > array[i].order) {
				key = i;
				i = 0;
			} else {
				i++;
			}
		}
		return key;
	}


	/**
	 * sort_benefits
	*/
	sort_benefits(benefits)
	{
		let formated = [];
		for (let key in benefits) { if (!benefits.hasOwnProperty(key)) { continue; }
			if (key === '_isGroup') {
				continue;
			}
			let trieda = '';
			switch (benefits[key].result) {
				case '0':
					trieda = 'incl_no';
					break;
				case '1':
					trieda = 'incl_yes';
					break;
				case '2':
					trieda = 'incl_special';
					break;
				default:
					// case '3' or other, doesnt display
					trieda = 'incl_hide';
			}
			formated.push(
				{
					title: benefits[key].formula_name,
					class: trieda,
				}
			);
		}
		return formated;
	}


	/**
	 * price_for_period
	*/
	price_for_period(result, period)
	{
		if (period === 1) {
			return result?.result?.rocne?.result;
		} else if (period === 2) {
			return result?.result?.polrocne?.result;
		} else if (period === 4) {
			return result?.result?.stvrtrocne?.result;
		} else if (period === 12) {
			return result?.result?.mesacne?.result;
		}
		return ''; // should not happen
	}


	/**
	 * label_for_period
	*/
	label_for_period(result, period)
	{
		if (result?.result?.price_label?.result !== undefined) {
			return result.result.price_label.result;
		}
		if (period === 1) {
			if (STATE_core().product_group_id() === 7) {
				return '';
			}
			return 'ročne';
		} else if (period === 2) {
			return 'polročne';
		} else if (period === 4) {
			return 'štvrťročne';
		} else if (period === 12) {
			return 'mesačne';
		}
		return ''; // should not happen
	}


	/**
	 * error_tooltip
	*/
	error_tooltip(result)
	{
		if (result?.errors !== undefined) {
			return result?.errors?.join('<br><br>');
		}
		return '';
	}


	/**
	 * info_tooltip
	*/
	info_tooltip(result)
	{
		return result?.next?.next_comment?.result || '';
	}


	/**
	 * check_alt_text
	*/
	check_alt_text()
	{
		let group = STATE_core().product_group_id();
		if (group === 3/*hypo*/ || group === 6/*sporenie*/ || group === 85/*dss*/ || group === 86/*dds*/) {
			return true;
		}
		return false;
	}


	/**
	 * find_period
	*/
	find_period(response)
	{
		let call_params = response.result.call_params;
		for (let key in call_params) { if (!call_params.hasOwnProperty(key)) { continue; }
			if (Functions.ends_with(key, 'frekvenciaNP') && !Functions.starts_with(key, '__')) {
				return parseInt(call_params[key], 10);
			}
		}
		// returns INT 1 | 2 | 4 | 12
		// if session doesnt have frekvenciaNP param yet (before calculate), create its default
		// its different for every group, and should be reworked in the future
		let id = STATE_core().product_group_id();
		return (id === 4/*zp*/ || id === 3/*hypo*/ || id === 6/*sporenie*/ || id === 85/*dss*/ || id === 86/*dds*/) ? 12 : 1;
	}


	/**
	 * search_for_udaje
	*/
	search_for_udaje(response)
	{
		let call_params = response.result?.call_params;
		for (var key in call_params) { if (!call_params.hasOwnProperty(key)) { continue; }
			if (key.indexOf('udaje') === -1) {
				continue;
			}
			return call_params[key];
		}
		return ''; // should not happen
	}


	/**
	 * find_order
	*/
	find_order(response, former_order, product_id)
	{
		let period = this.find_period(response);
		let order = [];
		// soon we will make it for all groups equaly, REMOVE AFTER WHILE, 08.09.2023
		let use_prefer = true; // STATE_core().product_group_id() === 7/*cestovne*/ || STATE_core().product_group_id() === 81/*zdravotka*/;
		if (period === 1) {
			order = (use_prefer ? response.result.order?.['rocne_prefer'] : response.result.order?.['rocne']);
		}
		else if (period === 2) {
			order = (use_prefer ? response.result.order?.['polrocne_prefer'] : response.result.order?.['polrocne']);
		}
		else if (period === 4) {
			order = (use_prefer ? response.result.order?.['stvrtrocne_prefer'] : response.result.order?.['stvrtrocne']);
		}
		else if (period === 12) {
			order = (use_prefer ? response.result.order?.['mesacne_prefer'] : response.result.order?.['mesacne']);
		}
		if (response.result[product_id]?.result?.result?.order_sort?.result === 'desc') {
			order.reverse();
		}
		// merge with old array, so we wont lose placeholders
		// first session's calculates have only finished products in order
		if (former_order !== null) {
			order = order.concat(former_order);
		}
		// remove duplicates from array
		order = [...new Set(order)];
		return order;
	}


	/**
	 * product_form_formatter
	*/
	product_form_formatter(form, is_product_string, filter_spoluucast)
	{
		if (is_product_string) {
			if (form === undefined) {
				return false;
			}
			form = JSON.parse(form);
		}
		let inputs = {};
		let specials = {};
		for (let tab in form) {
			// tabs above 100 are shown only in extended_settings
			if (parseInt(tab, 10) > 100) {
				continue;
			}
			for (let order in form[tab]) {
				for (let field_id in form[tab][order]) {
					if (Functions.ends_with(field_id, 'udaje')) {
						continue;
					} else if (Functions.ends_with(field_id, '_spoluucast') || Functions.ends_with(field_id, '_krytie')) {
						specials[field_id] = form[tab][order][field_id];
						continue;
					}
					if (!Show_hide.visibility_boolean(form[tab][order])) {
						continue;
					}
					inputs[field_id] = form[tab][order][field_id];
				}
			}
		}
		if (filter_spoluucast) {
			return Object.keys(specials).length > 0 ? specials : false;
		}
		return Object.keys(inputs).length > 0 ? inputs : false;
	}


	/**
	 * extended_form_formatter
	*/
	extended_form_formatter(string)
	{
		let form = JSON.parse(string || '{}');
		// remove tabs lower than 100, those are shown only in product card
		for (let tab in form) {
			if (parseInt(tab, 10) <= 100) {
				delete form[tab];
			}
		}
		// dont use this feature when no tabs are above 100
		if (Object.keys(form).length < 1) {
			return null;
		}
		return form;
	}


	/**
	 * flatten_extended_errors
	*/
	flatten_extended_errors(response, string)
	{
		let extended_inputs = this.extended_form_formatter(string);
		if (response.result?.validform_with_global_0 !== false || extended_inputs === null) {
			return null;
		}
		let tabs = response.result?.form_validation_error_tab_elements;
		let flat = {};
		for (let tab in tabs) {
			for (let field_id in tabs[tab]) {
				for (let tab_order in extended_inputs) {
					for (let input_order in extended_inputs[tab_order]) {
						if (Functions.first_key(extended_inputs[tab_order][input_order]) === field_id) {
							flat[field_id] = tabs[tab][field_id];
						}
					}
				}
			}
		}
		if (Object.keys(flat) < 1) {
			return null;
		}
		return flat;
	}


	/**
	 * validate_product
	*/
	validate_product(condition_string)
	{
		let target = condition_string.match(/\$(.*?) /);
		if (target === null) {
			console.log('condition in bad format', condition_string);
			return {
				error: false,
				input_name: '',
			};
		}

		condition_string = condition_string.replace(target[0], STATE_params().get_user_input(target[1]) + ' ');

		if (eval(condition_string)) {
			return {
				error: false,
				input_name: '',
			};
		}

		return {
			error: true,
			input_name: target[1],
		};
	}
}

export default new Product_helpers;
