<template>
	<div
		v-if="not_shown_in_technical_steps"
		class="upper_nav expanding-label"
		data-cy="upper_navigation"
	>

		<!-- left -->
		<button
			v-if="show_back"
			:disabled="loading"
			@click="prev"
			type="button"
			class="btn btn-exp btn-light btn-left btn-m"
		>
			<span class="btn-exp__label">Naspäť</span>
		</button>


		<!-- delete -->
		<button
			@click="clear"
			:disabled="loading"
			type="button"
			class="btn btn-exp btn-white btn-delete btn-m"
			:class="show_new ? 'expanded' : ''"
		>
			<span class="btn-exp__label">{{ show_new ? 'Vytvoriť nové porovnanie' : 'Vymazať' }}</span>
		</button>

		<!-- right -->
		<button
			v-if="show_next"
			:disabled="loading"
			@click="next"
			type="button"
			class="btn btn-exp expanded btn-primary btn-right btn-m"
		>
			<span class="btn-exp__label">Pokračovať</span>
		</button>

		<!-- down -->
		<button
			v-if="show_down"
			:disabled="loading"
			@click="down"
			type="button"
			class="btn btn-exp expanded btn-primary btn-down btn-m"
		>
			<!-- <span class="btn-exp__label">{{ step === 2 ? 'Vyberte' : 'Potvrďte' }}</span> -->
			<span class="btn-exp__label"></span>
		</button>

	</div>
</template>

<script>
	import STATE_modals from '@/store/STATE_modals.js';
	import STATE_core from '@/store/STATE_core.js';
	import STATE_nav from '@/store/STATE_nav.js';
	import STATE_forms from '@/store/STATE_forms.js';

	export default {
		props: {
		},
		components: {
		},
		emits: [],
		data() {
			return {
			}
		},
		created() {
		},
		methods: {
			prev()
			{
				STATE_nav().set_requested_direction('backward');
			},
			next()
			{
				STATE_nav().set_requested_direction('forward');
			},
			clear() {
				STATE_modals().open_it('Cleaning_session');
			},
			down()
			{
				let scroll_targets = {
					2: 'product_list_scroll_anchor',
					4: 'proposal_scroll_anchor',
				};
				let scroll_element = document.querySelector('#osm_app .' + scroll_targets[this.step]);
				let position = scroll_element.getBoundingClientRect().top + window.pageYOffset - 60;
				window.scrollTo({top: position, behavior: 'smooth'});
			},
		},
		computed: {
			step()
			{
				return STATE_core().current_step();
			},
			show_back()
			{
				if (this.step === 1 && STATE_forms().first_tab_is_current()) {
					return false;
				}
				return this.step > 0 && this.step < 5;
			},
			show_next()
			{
				return this.step > 0 && this.step < 4 && this.step !== 2;
			},
			show_down()
			{
				// in myport scroll doesnt work
				return STATE_core().get_backend_type() !== 'myport' && (this.step === 2 || this.step === 4);
			},
			loading()
			{
				return STATE_modals().is_loading();
			},
			not_shown_in_technical_steps()
			{
				return this.step < 10;
			},
			show_new()
			{
				return this.step >= 5;
			},
		},
		watch: {
		},
	}
</script>
